<template>
  <div class="catalog">
    <Titletop> </Titletop>
    <h2>{{ name }}</h2>
    <div>
      <div v-for="(item, index) in section" :key="item.id">
        <div @click="show.splice(index, 1, !show[index])" class="chapter_name">
          第{{ item.chapter_num }}卷
          <img src="@/assets/img/yjt.png" v-show="!show[index]" />
          <img src="@/assets/img/xjt.png" v-show="show[index]" />
        </div>
        <div v-show="show[index]">
          <div v-for="items in item.directory" :key="items.id" class="chapdiv">
            <a @click="Iflogin(items.is_pay,items.page)">
              <img class="yjt" src="@/assets/img/yjt.png" />
              <p>{{ items.title }}</p>
              <img
                v-if="items.is_pay"
                class="st"
                src="@/assets/img/st.png"
                alt=""
              />
            </a>
          </div>
        </div>
      </div>
    </div>
    <Iflogin></Iflogin>
    <MyLoading></MyLoading>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import Iflogin from "@/components/Iflogin.vue";
import MyLoading from "@/components/MyLoading.vue";
export default {
  name: "Mycatalog",
  props: ["id", "name"],
  components: { Titletop, Iflogin ,MyLoading},
  data() {
    return {
      section: [],
      show: [],
    };
  },
  mounted() {
    this.$bus.$emit("MyLoading",true);
    this.axios
      .get(`https://jyapi.jyacg.com/web/books/directory?books_id=${this.id}`, {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then((res) => {
        if (res.status == 200) {
          this.section = res.data.data;
          this.show = this.section.map((a) => {
            return false;
          });
          this.$bus.$emit("MyLoading",false);
        }
      });
  },
  methods: {
    Iflogin(is_pay,page) {
      if (is_pay) {
        if (!localStorage.getItem("api_token")) {
          this.$bus.$emit("Iflogin");
          return;
        }
      }
      this.$router.push({
        name: "read",
        query: {
          id: this.id,
          page
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.catalog {
  h2 {
    width: 300px;
    font-size: 17.5px;
    color: #333;
    font-weight: bold;
    margin: 15px 0px;
    padding: 0px 37.5px;
  }
  & > div {
    .chapter_name {
      display: flex;
      padding: 0px 20px;
      border-bottom: 1px solid #e4e4e4;
      height: 46px;
      justify-content: space-between;
      align-items: center;
      font-size: 16px;
      img:nth-child(1) {
        height: 15px;
      }
      img:nth-child(2) {
        width: 15px;
      }
    }
  }
  .chapdiv {
    a {
      display: flex;
      font-size: 12px;
      align-items: center;
      height: 40px;
      border-bottom: 1px solid #e4e4e4;
      color: #333;
      position: relative;
      .yjt {
        height: 15px;
        padding: 0px 12px 0px 20px;
      }
      .st {
        position: absolute;
        height: 13px;
        right: 20px;
      }
    }
  }
}
</style>